.button {
    padding: 10px;
    background-color: #002b36;
    border-radius: 20px;
    border: none;
    font-size: 15px;
    color: #EEFFFF;
    outline: none;
    -webkit-box-shadow: 0 3px #6d6d6d;
            box-shadow: 0 3px #6d6d6d;
}

.button:active {
    background-color: #015064;
    -webkit-box-shadow: 0 4px #666;
            box-shadow: 0 4px #666;
    -webkit-transform: translateY(4px);
        -ms-transform: translateY(4px);
            transform: translateY(4px);
  }

.btn-transparent {
    border-radius: 20px;
    border: none;
    background-color: #263238;
    outline: none;
}


